export default {
	data() {
		return {
			zscode: '',
		}
	},

	methods: {
		createdClick(){
			this.scode()
		},
		confirm() {
			this.$common.get("/trace-api/trace/changeOutCode?outCode=" + this.zscode).then((res) => {
				console.log(res)
				if (Number(res.data.code) === 200) {
					uni.showToast({
						title: res.data.message,
						duration: 2000
					});
					setTimeout(() => {
						uni.navigateBack({
							delta: 1
						});
					}, 1500)
				} else {
					uni.showToast({
						title: res.data.message,
						duration: 2000,
						icon: "none"
					});
				}
			})
		},
		scode() {
			uni.scanCode({
				success: (res) => {
					this.$common.tip("扫码成功", "success")
					this.zscode = res.result
				}
			});
		}
	}
}
